.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 4rem;
}
.greet-main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 2rem;
}

.gretting-main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
}

.greeting-main {
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  justify-content: space-between;
  gap: 1rem;
}

.greeting-main > * {
  flex: 1;
  /* margin-bottom: 30px; */
}
.button-greeting-div {
  display: flex;
  margin-top: 20px;
}

.greeting-text {
  margin-top: 80px;
  font-size: 70px;
  line-height: 1.1;
  font-family: "Google Sans Bold";
}

.greeting-nickname {
  font-size: 30px;
  line-height: 0px;
  font-family: "Google Sans Bold Italic";
}

.greeting-text-p {
  font-size: 1rem;
  line-height: 40px;
  margin-right: 40px;
  font-family: "Google Sans Medium";
  /* text-align: left; */
}

.greeting-image-div > * {
  max-width: 100%;
  height: auto;
  border-radius: 100%;
  object-fit: cover; /* Ensure the image covers the area without stretching */
  overflow: hidden;
}

/* Media Query */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .button-greeting-div {
    justify-content: space-around;
  }
  .greeting-text {
    font-size: 30px;
    margin-top: 0px;
    text-align: left;
  }
  .greeting-nickname {
    font-size: 25px;
    /* text-align: center; */
  }
  .greeting-text-p {
    font-size: 16px;
    margin-right: 0px;
    line-height: normal;
    /* text-align: center; */
  }
  .greeting-main {
    display: flex;
    flex-direction: column;
    /* /* margin: 0rem 0.5rem; */
  }

  .portfolio-repo-btn-div {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
